*{
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Almarai', sans-serif;
}

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

/* Small */
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}

/* Medium */
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}

/* Large */
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}


/* Navbar */

.nav{
  background-color: #3e3e3e;
  padding: 10px 0;
}

.nav .container{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav .container h2{
  margin: 0;
  color: #FFF;
  cursor: pointer;
}

.nav .container input{
  outline: none;
  border: none;
  padding: 7px 15px;
  min-width: 250px;
  font-size: 16px;
  font-weight: 600;
  color: #777;
  border-radius: 10px;
  border: none;
  transition: .3s;
}

.nav .container input:focus{
    box-shadow: 0 0 30px #FFFFFF30;
}


/* Content */

.content{
  padding: 40px 0;
  min-height: calc(100vh - 138px);
  background-color: #FAFAFA;
}

.content .container{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 40px;
}

.content .container .main-title{
  color: #3e3e3e;
  position: relative;
}

.content .container .main-title::before{
  content: "";
  position: absolute;
  top: 70px;
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  height: 5px;
  background-color: orange;
}

.content .container .categories{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.content .container .categories .cat{
  outline: none;
  border: none;
  background: none;
  padding: 5px 18px;
  font-size: 20px;
  font-weight: 600;
  color: #5e5e5e;
  border: 2px solid orange;
  border-radius: 5px;
  cursor: pointer;
  transition: .4s;
}

.content .container .categories .cat:hover{
  background-color: orange;
  color: #FFF;
}

.content .container .items{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
  width: 100%;
}

.content .container .items .box{
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 100%;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 20px #00000005;
  background-color: #FFF;
  cursor: pointer;
  transition: .4s;
}

.content .container .items .box:hover{
  box-shadow: 0 0 20px #00000015;
}

.content .container .items .box .text{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 30px;
  padding: 10px 20px;
  width: 100%;
}

.content .container .items .box .top{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.content .container .items .box .top h3{
  font-size: 21px;
}

.content .container .items .box .top h3:last-child{
  color: orange;
}

.content .container .items .box p{
  font-size: 16px;
  color: #777;
  line-height: 1.5;
  width: 100%;
}

@media(max-width:500px){
  .nav .container h2{
    font-size: 18px;
    margin: 8px 0;
  }
  .nav .container input{
    min-width: 150px;
  }
  .content .container .items .box{
    flex-direction: column;
    min-width: 90%;
  }
  .content .container .items .box img{
    width: 100%;
  }
  .content .container .items .box .text{
    width: 90%;
    text-align: center;
    gap: 15px;
    padding-bottom: 20px;
  }
  .content .container .items .box .top{
    width: 100%;
    padding: 10px 20px 0;
  }
}